<template>
    <div class="addWarp">
        <header>
            <router-link to="/home">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div>新增地址</div>
        </header>
        <div class="add_div">
            <section class="add_section">
                <input type="text" placeholder="请填写你的名字" v-model="name">
                <p v-if="isShow_P">请填写你的名字</p>
            </section>
            <section class="add_section">
                <router-link to="/addDetail">
                    <input type="text" placeholder="小区/写字楼/学校等" v-model="address" @click="chooseAddBtn">
                </router-link>
            </section>
            <section class="add_section">
                <input type="text" placeholder="请填写详细送餐地址" v-model="address1">
            </section>
            <section class="add_section">
                <input type="text" placeholder="请填写能联系到您的手机" v-model="phone">
            </section>
            <section class="add_section2">
                <input type="text"placeholder="备用联系电话(选填)" v-model="phone_bk">
            </section>
            <section class="add_btn" @click="addLoction">新增地址</section>
        </div>
    </div>

</template>

<script>
    export default {
        name: "Add",
        data(){
            return {
                isShow_P:false,//下面提示字的显隐
                name:'',
                address:'',
                address1:'',
                phone:'',
                phone_bk:"",//备注电话
                userId:'',//接收用户名id
                tag:'null',//标签
                sex:1,//性别

                tag_type:'3',//标签类型，2:家，3:学校，4:公司
                poi_type:'0',//类型默认：0


            }
        },
        created(){
            this.userId = localStorage.getItem("user_id")
            this.address = this.$store.state.addressInfo.name;
            this.name = this.$store.state.userName;
            // localStorage.setItem("userNameId",name);
        },
        methods:{
            chooseAddBtn(){
                this.$store.state.userName=this.name;
            },
            addLoction(){
                this.axios.post("/v1/users/"+this.userId+"/addresses",{
                    user_id:this.userId,
                    name:this.name,//姓名
                    address:this.$store.state.addressInfo.address,//地址
                    address_detail:this.address1,
                    geohash:this.$store.state.addressInfo.geohash,
                    phone:this.phone,
                    tag:this.tag,
                    sex:this.sex,
                    poi_type:this.poi_type,
                    phone_bk:this.phone_bk,
                    tag_type:this.tag_type
                }).then(res=>{
                     console.log(res);
                    if (res.success==="添加地址成功") {
                        this.$router.push({path:'address'})
                        this.$store.state.userName='';
                        // this.$store.state.addressInfo='';
                    }
                }).catch(err=>{

                })
            }
        }
    }
</script>

<style scoped>
    .addWarp {
        padding-top: 1.95rem;
    }
    .addWarp a{
        text-decoration: none;
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>div {
        width: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight:700;
    }
    header>a {
        text-decoration: none;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }

    .add_div{
        width: 100%;
        /*background-color: #fff;*/
        margin-top: .5rem;
    }
    .add_section{
        background: #fff;
        padding-top: .4rem;
    }
    .add_section p{
        font-size: .5rem;
        color: red;
        margin-left: .5rem;
        padding-top: .3rem;
    }
    .add_section2{
        background: #fff;
        padding-top: .4rem;
        padding-bottom: .4rem;
    }
    .add_div input{
        display: block;
        width: 14.5rem;
        font-size: .6rem;
        margin: 0 auto;
        padding: .3rem;
        background: #f2f2f2;
        border: 1px solid #ddd;
        border-radius: 3px;
    }
    .add_btn{
        text-align: center;
        margin: .6rem auto;
        width: 14.5rem;
        background: #4cd964;
        border-radius: 3px;
        font-size: .6rem;
        color: #fff;
        line-height: 1.6rem;
        font-weight: 700;
        opacity: .6;
    }
</style>